<template>
    <div class="Header mb-3">
        <!--b-navbar toggleable="lg" type="dark" variant="success">
            <b-navbar-brand to="/">主頁</b-navbar-brand>

            <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

            <b-collapse id="nav-collapse" is-nav>
                <b-navbar-nav>
                    <b-nav-item to="/KMB">九巴/龍運</b-nav-item>
                    <b-nav-item to="/NWFB">新巴</b-nav-item>
                    <b-nav-item to="/Citybus">城巴</b-nav-item>
                    <b-nav-item to="/Minibus">小巴</b-nav-item>
                    <b-nav-item to="/MTR">港鐵</b-nav-item>
                    <b-nav-item to="/MTRBus">港鐵巴士</b-nav-item>
                    <b-nav-item to="/LightRail">輕鐵</b-nav-item>
                    <b-nav-item to="/NLB">大嶼山巴士</b-nav-item>
                    <b-nav-item to="/Ferry">渡輪</b-nav-item>
                    <b-nav-item to="/WaterTaxi">水上的士</b-nav-item>
                </b-navbar-nav>
            </b-collapse>
        </b-navbar-->

        <b-navbar toggleable="lg" type="dark" variant="success">
            

            <b-navbar-brand to="/" class="ml-lg-5 brand">
                <div>
                    <img src="../assets/clock.png" height="24" class="mr-2 mb-1"/>
                    <span>主頁</span>
                </div>
                
            </b-navbar-brand>

            <b-navbar-toggle target="nav-collapse2"></b-navbar-toggle>

            <b-collapse id="nav-collapse2" is-nav>
                <b-navbar-nav>
                    <b-nav-item v-for="(t, i) in transportation" :key="t.id" :to="t.to" :active="isActiveNavItem[i]">{{ t.name_tc }}</b-nav-item>
                </b-navbar-nav>
            </b-collapse>
            
        </b-navbar>

    </div>
    
</template>

<script>
//import 'bootstrap/dist/css/bootstrap.css'
//import 'bootstrap-vue/dist/bootstrap-vue.css'

let transportation = [
    {to: '/KMB', name_tc: '九巴/龍運', code: 'KMB'},
    {to: '/NWFB', name_tc: '新巴', code: 'NWFB'},
    {to: '/Citybus', name_tc: '城巴', code: 'Citybus'},
    {to: '/Minibus', name_tc: '小巴', code: 'Minibus'},
    {to: '/MTR', name_tc: '港鐵', code: 'MTR'},
    {to: '/MTRBus', name_tc: '港鐵巴士', code: 'MTRBus'},
    {to: '/LightRail', name_tc: '輕鐵', code: 'LightRail'},
    {to: '/NLB', name_tc: '大嶼山巴士', code: 'NLB'},
    {to: '/Ferry', name_tc: '渡輪', code: 'Ferry'},
    {to: '/WaterTaxi', name_tc: '水上的士', code: 'WaterTaxi'},
];

export default {
    name: "HeaderTitle",
    props: {
        t_type: String
    }, 
    data() {
        return {
            transportation,
        }
    },
    computed: {
        
        isActiveNavItem(){
            let props_t_type = this.t_type;
            let t_cat = this.t_type;
            return this.transportation.map(function(t){ 
                
                let pos = (props_t_type+'').indexOf('_');
                
                if (pos >= 0){
                    t_cat = props_t_type.substring(0, pos);
                }

                if (t_cat == t.code){
                    return true;
                }
                else return false;

            })
        }
    },
    
    method: {
        


    },


}

</script>

<style scoped>
.brand{
    color: #038 !important;
