import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter ({
    routes: [
        {
            path: '/',
            name: 'HomePage',
            component: () => 
                import("../views/Home.vue"),
        },
        {
            path: '/KMB',
            name: 'KMB',
            component: () => 
                import("../views/KMB.vue"),
        },
        {
            path: '/KMB/:route/:bound/:service_type',
            name: 'KMB_Route',
            component: () => 
                import("../views/KMB_Route.vue"),
        },
        {
            path: '/Citybus',
            name: 'Citybus',
            component: () => 
                import("../views/Citybus.vue"),
        },
        {
            path: '/Ferry',
            name: 'Ferry',
            component: () => 
                import("../views/Ferry.vue"),
        },
        {
            path: '/Minibus',
            name: 'Minibus',
            component: () => 
                import("../views/Minibus.vue"),
        },
        {
            path: '/LightRail',
            name: 'LightRail',
            component: () => 
                import("../views/LightRail.vue"),
        },
        {
            path: '/MTR',
            name: 'MTR',
            component: () => 
                import("../views/MTR.vue"),
        },
        {
            path: '/MTRBus',
            name: 'MTRBus',
            component: () => 
                import("../views/MTRBus.vue"),
        },
        {
            path: '/NLB',
            name: 'NLB',
            component: () => 
                import("../views/NLB.vue"),
        },
        {
            path: '/NWFB',
            name: 'NWFB',
            component: () => 
                import("../views/NWFB.vue"),
        },
        {
            path: '/WaterTaxi',
            name: 'WaterTaxi',
            component: () => 
                import("../views/WaterTaxi.vue"),
        },
        {
            path: '*',
            name: 'PageNotFound',
            component: () => 
                import("../views/PageNotFound.vue"),
        }
    ],
    //mode: 'history'     
})

export default router
